<template>
    <div class="boss_box">
        <div class="top_box">
            <div class="top_center">
                <button :class="activeTab == '国家' ? 'active' : ''"
                        @click="tabClick('国家')">国家</button>
                <button :class="activeTab == '市级' ? 'active' : ''"
                        @click="tabClick('市级')">市级</button>
                <button :class="activeTab == '区级' ? 'active' : ''"
                        @click="tabClick('区级')">区级</button>
            </div>
        </div>
        <div class="table_boxStl">
            <div class="center-card">
                <div class="tab">
                    <!-- <a
                        class="tab-btn"
                        href="javascript:;"
                        :class="activeTab == '国家' ? 'active' : ''"
                        @click="tabClick('国家')"
                        >国家</a
                    >
                    <a
                        class="tab-btn"
                        href="javascript:;"
                        :class="activeTab == '市级' ? 'active' : ''"
                        @click="tabClick('市级')"
                        >市级</a
                    >
                    <a
                        class="tab-btn"
                        href="javascript:;"
                        :class="activeTab == '区级' ? 'active' : ''"
                        @click="tabClick('区级')"
                        >区级</a
                    > -->
                    <el-input
                        placeholder="请输入政策关键字搜索" 
                        v-model="searchIpt" 
                        class="searchStl">
                        <el-button @click="requestTableData" slot="append" icon="el-icon-search" class="serachIcon"></el-button>
                    </el-input>
                </div>

                <div class="table_box">
                    <div class="table_header">
                        <span style="width: 200px">序号</span>
                        <span style="width: 520px">文件名称</span>
                        <span style="width: 520px">文号</span>
                        <span style="width: 520px">发文单位</span>
                        <span style="width: 520px">发布日期</span>
                    </div>
                    <div class="table_body_box">
                        <div class="table_body" v-for="(item,index) in tableData" :key="index">
                            <span style="width: 150px">{{index + 1}}</span>
                            <span style="width: 525px">
                                <a :href="item.weburl" target ="_blank">{{item.policyName}}</a>
                            </span>
                            <span style="width: 525px">{{item.documentNumber}}</span>
                            <span style="width: 525px">{{item.publishDepartment}}</span>
                            <span style="width: 525px">{{item.createTime}}</span>
                        </div>
                    </div>
                    
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import {http} from '@/utils/request'
import Breadcrumb from "@/components/Breadcrumb";
export default {
    data() {
        return {
            tableData: [],
            searchIpt: '',
            activeTab: "国家",
        }
    },
    components: {
        Breadcrumb,
    },
    mounted() {
        this.requestTableData();
    },
    methods: {
        tabClick(activeTab){
            this.activeTab = activeTab;
            this.tableData = [];
            this.requestTableData();
        },
        requestTableData() {
            let params = {
                level: this.activeTab,
                policyName: this.searchIpt
            }
            http.get('/policy_plan/policyPlanQueryPage',{params}).then(res => {
                this.tableData = res.data.result;
                this.tableData.forEach(item => {
                    item.createTime = item.createTime.slice(0, 10)
                })
            })
        },
    }
}
</script>

<style lang="scss" scoped>
.boss_box {
    // width: 2479px;
    height: 1283px;
    border: 1px solid #49ABFA;
    padding: 24px;
    .top_box {
        width: 100%;
        height: 133px;
        .top_center {
            width: 1000px;
            margin: 0 auto;
            text-align: center;
            button {
                display: inline-block;
                width: 272px;
                height: 61px;
                margin-right: 33px;
                font-size: 30px;
                background: #439799;
                color: #fff;
                border: none;
                cursor: pointer;
                &.active{
                    background: #24c9cc;
                }
            }
        }
    }
    .table_boxStl {
        width: 2430px;
        height: 969px;
        // background: red;
        margin: 0 auto;
        
    }
    .center-card {
        // border: 1px solid #49ABFA;
        border-radius: 20px;
        padding: 50px;
        height: 969px;
        .table_box {
            .table_header {
                span {
                    height: 70px;
                    display: inline-block;
                    line-height: 70px;
                    font-size: 26px;
                    text-align: center;
                    color: #fff;
                }
                border-bottom: 1px solid #439799;
            }
            .table_body_box{
                max-height: 969px;
                overflow-y:auto ;
            }
            .table_body {
                span {
                    height: 70px;
                    display: inline-block;
                    line-height: 70px;
                    font-size: 26px;
                    text-align: center;
                    color: #fff;
                    overflow: hidden;
                    text-overflow:ellipsis;
                    white-space: nowrap;
                }
                border-bottom: 1px solid #439799;
            }
        }
        .tab {
            position: relative;
            .searchStl {
                width: 500px;
                height: 40px;
                background: rgba(74, 129, 169, 0);
                padding-left: 20px;
                color: #fff;
                position: absolute;
                right: 200px;
                top:-45px;
                .el-input__inner{
                    background: none;

                }
            }
            .tab-btn{
                font-size: 26px;
                line-height: 29px;
                display: inline-block;
                padding:0 25px 20px;
                border-bottom:2px solid #439799;
                &.active{
                    color:#439799;
                }
            }
        }
    }
}
</style>
<style lang="scss">
    .searchStl {
        .el-input__inner{
            color:#fff;
            background: none;
            border-radius: 20px;
            border:1px solid #439799;
            padding-right:70px;
        }
        .el-input-group__append{
            background:none;
            border:none;
            position: relative;
            left:-60px;
        }
        .el-icon-search{
            color:#eee;
            font-size:20px;
        }
    }
</style>